import React, { Component } from 'react';
import {
  Empty, Button, Divider, Checkbox, Popconfirm,
} from 'antd';
import {
  SettledData,
  EnteredData,
} from './const';
import { PageDrawer } from '$pages/components/PageLayouts';
import style from './ShoppingCart.module.scss';

export default class ShoppingCart extends Component {
  state = { }

  renderRecord = (data) => {
    if (data instanceof Array) {
      return (
        <dl className={style.dl}>
          <dt className={style.dt}>{`(${data.length})`}</dt>
          {
          data.length > 0 && (
          <dd className={style.dd}>
            <Checkbox.Group className={style.group}>
              {
                data.map((item, j) => (
                  <div className={style.item} key={j}>
                    <div className={style.itemCont}>
                      <Checkbox value={j}>{item.title}</Checkbox>
                    </div>
                    <div className={style.itemCtrl}>
                      <a></a>
                      <Divider type="vertical" />
                      <a></a>
                    </div>
                  </div>
                ))
              }
            </Checkbox.Group>
          </dd>
          )
        }
        </dl>
      );
    }
    return <Empty />;
  }

  renderColumn = (data) => {
    if (data instanceof Array) {
      return (
        <dl className={style.dl}>
          <dt className={style.dt}>{`(${data.length})`}</dt>
          {
            data.length > 0 && (
            <dd className={style.dd}>
              <Checkbox.Group className={style.group}>
                {
                  data.map((item, j) => (
                    <div className={style.column} key={j}>
                      <Checkbox value={j} className={style.checkbox} />
                      <div className={style.colMain}>
                        <div className={style.colHead}>
                          {item.title}
                        </div>
                        <div className={style.colBody}>
                          <div className={style.section}>175866585981758665859917586658595</div>
                          <div className={style.section}>077120190320</div>
                          <div className={style.section}>IPTV077120190320</div>
                        </div>
                      </div>
                      <div className={style.colExtra}>
                        <div className={style.section}>
                          <span className={style.strong}>199.00</span>
                        </div>
                        <div className={style.section}>
                          <a></a>
                          <Divider type="vertical" />
                          <a></a>
                        </div>
                      </div>
                    </div>
                  ))
                }
              </Checkbox.Group>
            </dd>
            )
          }
        </dl>
      );
    }
    return <Empty />;
  }

  render() {
    const { visible, onClose } = this.props;
    return (
      <PageDrawer
        drawerProps={{
          title: '',
          visible,
          width: '600px',
          maskClosable: true,
          onClose: () => { onClose(); },
        }}
        footer={(
          <div className={style.footer}>
            <div className={style.ftMain}>
              <Popconfirm
                title=""
                okText=""
                cancelText=""
              >
                <Button></Button>
              </Popconfirm>
              <Popconfirm
                title=""
                okText=""
                cancelText=""
              >
                <Button></Button>
              </Popconfirm>
            </div>
            <div className={style.ftExtra}>
              <Button className="btn-warn"></Button>
            </div>
          </div>
        )}
      >
        <div className={style.main}>
          {this.renderRecord(SettledData)}
          {this.renderColumn(EnteredData)}
        </div>
      </PageDrawer>
    );
  }
}
