import React, { Component } from 'react';
import { Tabs, Empty, Button } from 'antd';
import { PageHead, PageFooter, PageTabs } from '$pages/components/PageLayouts';
import style from './UserInfoEdit.module.scss';

import BaseInfo from './BaseInfo'; // 
import Contacts from './Contacts'; // 
import ManageEvaluate from './ManageEvaluate'; // 
import ExtendInfo from './ExtendInfo'; // 
import Taxpayer from './Taxpayer'; // 

const { TabPane } = Tabs;

export default class UserInfoEdit extends Component {
  state = {
    activeKey: '1',
  }

  renderContent = () => {
    const { activeKey } = this.state;
    switch (activeKey) {
      case '1':
        return <BaseInfo />;
      case '2':
        return <Contacts />;
      case '3':
        return <ManageEvaluate />;
      case '4':
        return <ExtendInfo />;
      case '5':
        return <Taxpayer />;
      default:
        return <Empty />;
    }
  }

  render() {
    return (
      <div className={style.wrap}>
        <PageHead
          icon={<span className="iconfont ma-custer-hl" />}
          layout="fixed"
          title=""
          className={style.head}
        >
          <PageTabs
            defaultActiveKey="1"
            className={style.tabs}
            onChange={(key) => {
              this.setState({
                activeKey: key,
              });
            }}
          >
            <TabPane tab="" key="1" />
            <TabPane tab="" key="2" />
            <TabPane tab="" key="3" />
            <TabPane tab="" key="4" />
            <TabPane tab="" key="5" />
          </PageTabs>
        </PageHead>
        {this.renderContent()}
        <PageFooter>
          <Button></Button>
          <Button className="btn-warn"></Button>
        </PageFooter>
      </div>
    );
  }
}
