import React, { Component } from 'react';
import {
  Popover, Card, Input, Table,
} from 'antd';
import style from './NumberPopover.module.scss';
import { PageData } from './const';

const { Search } = Input;

export default class NumberPopover extends Component {
  state = { }

  renderSelectPopover = () => {
    const columns = [
      {
        title: '',
        key: 'name',
        width: '70%',
        dataIndex: 'name',
      },
      {
        title: '',
        width: '30%',
        key: 'number',
        dataIndex: 'number',
      },
    ];
    return (
      <Card
        bordered={false}
        title=""
        extra={<Search placeholder="" />}
      >
        <Table
          columns={columns}
          dataSource={PageData}
          size="middle"
          pagination={false}
          scroll={{
            y: 300,
          }}
        />
      </Card>
    );
  }

  render() {
    const { children } = this.props;
    return (
      <Popover
        content={this.renderSelectPopover()}
        trigger="click"
        overlayClassName={style.wrap}
      >
        {children}
      </Popover>
    );
  }
}
