import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import {
  Input, Select, Button, AutoComplete,
} from 'antd';
import classnames from 'classnames';
import style from './ClientOrientation.module.scss';

const { Option } = Select;

class ClientOrientation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchTypes: [],
    };
  }

  render() {
    const { className } = this.props;
    const { searchTypes } = this.state;
    return (
      <div className={classnames(style.main, className)}>
        <div className={style.group}>
          <Select defaultValue="" className={style.select} onSelect={this.handleTypeSelect}>
            {
              searchTypes.map(t => (<Option key={t.attrValue} value={t.attrValue}>{t.attrValueDesc}</Option>))
            }
          </Select>
          <AutoComplete
            dropdownMatchSelectWidth={false}
            className={style.input}
            dataSource={[]}
            placeholder=""
            onSearch={this.handleSearch}
            optionLabelProp="value"
          />
          <Button type="primary"></Button>
        </div>
        <Button className={classnames(style.btn, 'btn-warn')}></Button>
        <Button
          className={style.btn}
        >
            
        </Button>
        <Button
          className={style.btn}
        >
            
        </Button>
      </div>
    );
  }
}

export default connect(store => ({
  cust: store.cust,
  attrs: store.attrs,
}))(ClientOrientation);
