import React, { Component, Fragment } from 'react';
import {
  Button, Form, Select, Input,
} from 'antd';
import { PageDrawer } from '$pages/components/PageLayouts';
import style from './DrawerTerminal.module.scss';
import { PageData } from './const';

const { Option } = Select;
const { Search } = Input;

export default class DrawerTerminal extends Component {
  state = {
    selectable: PageData,
    selected: [],
  }

  renderItems = (items, edit) => {
    if (items instanceof Array && items.length > 0) {
      return (
        <ul className={style.list}>
          {
            items.map((item, index) => (
              <li
                className={style.item}
                key={index}
              >
                <div className={style.itemCont}>
                  {item.label}
                </div>
                {
                  (typeof edit === 'boolean' && edit) ? (
                    <a onClick={() => { this.onSelected(item.key, item); }} className="text-danger"></a>
                  ) : (
                    <a onClick={() => { this.onSelectable(item.key, item); }}></a>
                  )}

              </li>
            ))
          }
        </ul>
      );
    }
    return '';
  }

  onSelectable = (key, value) => {
    this.setState(prev => ({
      selectable: prev.selectable.filter(item => (item.key !== key)),
      selected: [
        ...prev.selected,
        value,
      ],
    }));
  }

  onSelected = (key, value) => {
    this.setState(prev => ({
      selected: prev.selected.filter(item => (item.key !== key)),
      selectable: [
        ...prev.selectable,
        value,
      ],
    }));
  }

  isValue = (value) => {
    if (typeof value === 'string' && value.length !== 0) {
      return value;
    }
    return '';
  }

  render() {
    const { visible, onClose } = this.props;
    const { selectable, selected } = this.state;
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 18 },
    };
    const formItemControl = {
      wrapperCol: { offset: 6, span: 18 },
    };
    return (
      <PageDrawer
        drawerProps={{
          title: '',
          visible,
          width: '1000px',
          maskClosable: true,
          onClose: () => { onClose(); },
        }}
        bodyClass={style.drawer}
        footer={(
          <Fragment>
            <Button onClick={() => { onClose(); }}></Button>
            <Button type="primary"></Button>
          </Fragment>
        )}
      >
        <div className={style.row}>
          <div className={style.col}>
            <div className={style.colTit}></div>
            <div className={style.colSearch}>
              <Search placeholder="" />
            </div>
            <div className={style.colContainer}>
              <div className={style.colMain}>
                {this.renderItems(selectable)}
              </div>
            </div>
          </div>
          <div className={style.col}>
            <div className={style.colTit}></div>
            <div className={style.colContainer}>
              <div className={style.colMain}>
                {this.renderItems(selected, true)}
              </div>
            </div>
          </div>
          <div className={style.col}>
            <div className={style.colTit}></div>
            <div className={style.colContainer}>
              <div className={style.colForm}>
                <Form
                  layout="horizontal"
                  className="form-horizontal"
                  {...formItemLayout}
                >
                  <Form.Item label="">
                    <Select
                      showSearch
                      placeholder=""
                    >
                      <Option value="0"></Option>
                    </Select>
                  </Form.Item>
                  <Form.Item label="">
                    <Input placeholder="" />
                  </Form.Item>
                  <Form.Item label="">
                    <Select
                      showSearch
                      placeholder=""
                    >
                      <Option value="0"></Option>
                    </Select>
                  </Form.Item>
                  <Form.Item label="">
                    <Input placeholder="" addonAfter="" />
                  </Form.Item>
                  <Form.Item label="">
                    <Input placeholder="" addonAfter="" />
                  </Form.Item>
                  <Form.Item {...formItemControl}>
                    <Button type="primary"></Button>
                    <Button></Button>
                  </Form.Item>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </PageDrawer>
    );
  }
}
