function labelClass(state) {
  const type = state.toLowerCase();
  switch (type) {
    case '':
      return 'primary';
    case '':
      return 'success';
    case '':
      return 'success';
    case 'itv':
      return 'success';
    case '':
      return 'warning';
    default:
      return null;
  }
}

export default labelClass;
